body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #231F20;
  background-color: #eef3f6;
}

*, *:before, *:after {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  max-width: 600px;
  overflow: auto;
  margin-left: auto;
  margin-right: auto;
}

.card {
  padding: 1em;
  margin: 1em;
  box-shadow: 0 1px 2px rgba(0,0,0,0.1), 0 2px 4px rgba(15,20,42,0.05);
  background-color: #f7f7f7;
  border-radius: 10px;
}

.header-img {
  height: 70px;
}
.header-img-right {
  float: right;
}

input.poke-search {
  background: #fff;
  border: 1px solid #dfe1e5;
  box-shadow: none;
  width: 100%;
  border-radius: 24px;
  height: 44px;
  margin: 0 auto;
  padding: 0px 16px 0 16px;
  font-size: 16px;
  outline: none;
}

.home-links {
  padding: 0 1em 1em;
}

.home-links a {
  color: #5955ef;
  text-decoration: none;
  margin-right: 1em;
}

.pokemon-card {
  display: flex;
  flex-flow: row wrap;
  border-bottom: 1px solid rgba(0,0,0,0.1);
  padding: 1em 0;
  color: #231F20;
  text-decoration: none;
  align-items: stretch;
  justify-content: stretch;
  justify-items: stretch;
}
.pokemon-card:last-child {
  border: none
}

.image-row {
  width: 5em;
  height: 5em;
  background: #F2F2F2;
}

img.pokemon-image {
  width: 100%;
}

.details-row {
  padding-left: 1em;
  flex: 1 1 auto;
}

.pokemon-number {
  color: #888;
  margin-right: .3em;
  font-size: 0.7em;
}

.pokemon-number::before {content: '#';}

span.pokemon-name {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.1em;
  margin-right: 0.5em;
}

span.pokemon-favorite {
  font-size: 2.5em;
  display: inline-block;
  margin-left: auto;
  line-height: 0.8;
  margin-top: -0.2em;
  margin-bottom: -0.2em;
  color: #e0dca7;
  background: #f2f2f2;
  border-radius: 50%;
  height: 1em;
  width: 1em;
  text-align: center;
}

p.pokemon-name-wrapper {display: flex;margin-bottom: 0.1em;align-items: center;}

span.pokemon-favorite.pokemon-favorite-true {
  color: #5a54f2;
}

span.pokemon-favorite:hover {
  background: #d8c290;
}

span.pokemon-favorite.pokemon-favorite-false:hover {
  color: white;
}

span.pokemon-type {
  background: #5a54f3;
  color: white;
  padding: 0.2em 0.6em 0.3em;
  margin-right: 0.4em;
  border-radius: 5px;
  font-size: 0.7em;
}

p.pokemon-types {
  margin-top: 0;
}

.stats-row {
  display: flex;
  flex-flow: row wrap;
  width: 100%;
  flex: 1 0 100%;
  justify-content: space-between;
}

span.pokemon-stat {
  font-size: 0.9em;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1px;
}

span.pokemon-stat {
  margin-right: 0.6em;
  width: 60px;
}

span.pokemon-stat-name {
  font-weight: normal;
}

span.pokemon-stat-value {
  margin-left: 0.2em;
}


span.pokemon-moves-title {
  font-size: 1.3em;
  font-weight: bold;
  margin: 0.4em 0;
  display: block;
}

span.pokemon-move-name {
  width: 25%;
  text-align: right;
  padding-right: 0.7em;
  font-weight: bold;
}

.pokemon-move {
  display: flex;
  flex-flow: row nowrap;
  border-top: 1px solid rgba(0,0,0,0.1);
  padding: 0.6em 0px;
}

.pokemon-page .pokemon-card {
  border-bottom: none;
}

.pokemon-move-manners-col {
  flex: 0 1 75%;
}

.pokemon-move-manners > * {
  display: block;
}

.pokemon-move-manners {
  margin-bottom: 0.3em;
  padding-bottom: 0.3em;
  border-bottom: 1px solid rgba(0,0,0,0.1);
}

.pokemon-move-manners:last-child {
  border-bottom: none;
  margin: 0;
  padding: 0;
}

.pokemon-move-manners b {
  color: #5a54f2;
  font-weight: normal;
}

@media (max-width: 500px) {
  .card {
      margin: 0;
      border-radius: 0;
      box-shadow: none;
  }

  .header-img {
      height: 30px;
  }

  .stats-row {
      justify-content: flex-start;
  }

  .pokemon-move {
      flex-flow: column;
  }

  span.pokemon-move-name {
      width: 100%;
      flex: 1 1 auto;
      text-align: left;
      margin-bottom: 0.3em;
  }

  .pokemon-move-manners-col {
      flex: 1 1 auto;
  }
}